import  React, { useRef }  from 'react'
import $ from 'jquery'
export default function FAQITEM({num,ques,ans}){
    let ramdom='accoridon-' +Math.round(Math.random()*1000000000)
    let thisRef=useRef()
    function togggle(){
        
        $(thisRef.current).find('.collapse').slideToggle()
    }
    return(
        <>
    <li className="list-group-item" ref={thisRef}>
                  {/* Toggle */}
                  <a className="dropdown-toggle d-block font-size-lg font-weight-bold text-reset" data-toggle="collapse" href={`#${ramdom}`} onClick={togggle} >
                    {num}. {ans}
                  </a>
                  {/* Collapse */}
                  <div className="collapse" id={ramdom} data-parent={`#${ramdom}`}>
                    <div className="mt-5">
                      <p className="mb-0 font-size-lg text-gray-500">
                       {ques}
                      </p>
                    </div>
                  </div>
                </li>
        </>
    )
}