import storelocationApi from "api/storelocationApi"
import { useEffect, useState } from "react"
import StoreItem from "./components/storeLocationItem"

export default function Store_locator() {
    let [list ,setList]=useState([])
    useEffect(()=> {
        storelocationApi.get().then(res =>{
            setList(res.data)
          })
    },[])
    let [map,setMap]=useState(["https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1959.2612087312148!2d106.78490560801636!3d10.84781333234938!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x317527131ae8b249%3A0x4d2d3c8fab7d3c2e!2zOTcgxJDGsOG7nW5nIE1hbiBUaGnhu4duLCBIaeG7h3AgUGjDuiwgUXXhuq1uIDksIFRow6BuaCBwaOG7kSBI4buTIENow60gTWluaCwgVmnhu4d0IE5hbQ!5e0!3m2!1svi!2s!4v1620748190449!5m2!1svi!2s"])
    function storeClick(i){
        setMap(list[i].goggle_map)
    }
    return (
        <div>
            {/* HEADER */}
            <section className="pt-7 pb-12">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-12 col-md-10 col-lg-8 col-xl-6">
                            {/* Heading */}
                            <h3 className="mb-10 text-center">Store Locator</h3>
                            {/* Search */}
                            <div className="input-group input-group-merge">
                                <input className="form-control" type="search" placeholder="Enter Country or City" />
                                <div className="input-group-append">
                                    <button className="btn btn-outline-border" type="submit">
                                        <i className="fe fe-search" />
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* MAP */}
            <section className="py-12 bg-light">
                <div className="container">
                    <div className="row">
                        <div className="col-12 col-md-5 col-lg-4">
                            {/* Card */}
                            <div className=" card card-xl h-md-0 minh-md-100 mb-10 mb-md-0   " style={{overflow:'auto'}}  >
                                {
                                    list.map((e,i) => <StoreItem onClick={storeClick.bind(null,i)}  key={i} {...e}/>)
                                }
                            </div>
                        </div>
                        <div className="col-12 col-md-7 col-lg-8">
                            {/* Map */}
                            <iframe src={map} width="100%" height="450"  allowfullscreen="" loading="lazy"></iframe>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}