
import './App.css';
import Header from './components/header'
import Footer from './components/footer';
import Home from './home';
import Product_detail from './page/productdetail';

import Page404 from './page/404'
import AppProvider from './core/AppProvider';
import { Route, Router, Switch } from 'react-router';
import reducers from './redux/reducers';
import Store_locator from './page/store-locator';
import Auth from './page/auth';
import Account from './page/account';
import Modal from './components/modal';
import PrivateRouter from './core/PrivateRouter';
import Catalog from './page/catallog';
import Checkout from 'page/checkout';
import CommingSon from 'page/CommingSon';
import renderRouter from 'core/routerConfig';
import routers from 'router';
import CountDow from 'hoc/widthCountDown';

import Contact from 'page/contactus';
import SizeChart from 'components/modal/compoments/sizeChart';
import ShopCart from 'components/modal/compoments/ShopCart';
import { useRef } from 'react';
import Faq from 'page/faq';

function App() {
  let sizeChartRef=useRef()
  return (
    <AppProvider reducers={reducers} >

      {/* <Modal /> */}
      <Header />
      <Switch>
        <Route path="/auth" component={Auth} />
        <Route path="/catalog" component={Catalog} />
        <Route path="/product_detail:slug" component={Product_detail} />
        <Route path="/checkout" component={Checkout} />
        <PrivateRouter path="/account" component={Account} />
        <Route path="/comming-son" component={() => <CountDow WrapCompoment={CommingSon} timeCoutDown={(1 * 24 + 5) * 60 * 60} />} />
        <Route path="/faq" component={Faq} />
        <Route path="/store_locator" component={Store_locator} />
        <Route path="/contact" component={Contact} />
        <Route path="/" exact component={Home} />
        <Route path="/" component={Page404} />
      </Switch>
      <ShopCart/>
   
      <Footer sizeChartRef={sizeChartRef} />
      <SizeChart ref={sizeChartRef} />
      {/* {renderRouter(routers)} */}
    </AppProvider>
  );
}

export default App;
